$size: 3rem;
.wrapper{
  display:block;
  position: relative;
  width: $size;
  height: $size;
    &:hover .picture{
      fill: rgb(193, 34, 34);
    }
}
.picture{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  fill: rgb(138, 134, 134);
}
.link{
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 2;
}

